.contact {
    margin-bottom: 138px;
}

.contact__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    max-width: 1366px;
    width: 100%;
    margin: 0 auto;
    padding-top: 130px;
}

.contacts__image-wrapper {
    padding-left: 98px;
    margin-right: 72px;
}

.contacts__content-wrapper {
    flex-grow: 1;
}

.contacts__headline {
    font-family: Oswald;
    font-style: normal;
    font-weight: 600;
    font-size: 52px;
    line-height: 60px;
    color: #061B28;
    margin: 0px;
    margin-bottom: 50px;
}

.contacts__text,
.socials.contact__socials {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 27px;
    color: #061B28;
    margin: 0px;
    margin-bottom: 6px;
}

.socials.contact__socials {
    margin-bottom: 10px;
}

.contacts__link {
    display: block;
    text-decoration: none;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 31px;
    color: #061B28;
    margin: 0px;
    margin-bottom: 26px;
}

.socials__list {
    display: flex;
    align-items: baseline;
    list-style: none;
    padding-left: 0px;
    font-size: 0px;
    overflow: hidden; 
}

.socials__link {
    display:inline-block;
    width: 22px;
    height: 22px;
    margin-right: 32px;
}

.socials__link--facebook {
    background-image: url("../images/facebook.svg");
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 28px;
}

.socials__link--instagram {
    background-image: url("../images/instagram.svg");
    background-repeat: no-repeat;
    background-position: center;
}

.socials__link--twitter {
    background-image: url("../images/twitter.svg");
    background-repeat: no-repeat;
    background-position: center;
}